/* src/App.css */
body {
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f5acf5; /* Purple color */
}

.color-picker-container {
  text-align: center;
}

h1 {
  margin-bottom: 20px;
}

.color-box {
  width: 300px;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  border: 2px solid #333;
  background-color: #d1b2ff;
}

.color-options {
  display: flex;
  justify-content: center;
}

.color-option {
  width: 50px;
  height: 50px;
  margin: 0 5px;
  cursor: pointer;
  border: 2px solid #333;
}
